* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

ul, ol, li {
  list-style: none; }

a {
  text-decoration: none;
  color: #000; }

body {
  background: linear-gradient(0deg, #f1c276 0%, #f287c2 100%) fixed; }

.header {
  position: relative; }
  .header header {
    width: 100%;
    height: 10vh;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center; }
    .header header i {
      font-size: 24px;
      margin-right: 10px; }
    .header header h1 {
      font: 700 32px/1 'Sansita';
      margin-right: 1vw; }
    .header header span {
      text-align: center;
      font: 20px/1 'Open Sans';
      display: inline-block;
      width: 25px;
      height: 25px;
      background: #ddd;
      border-radius: 50%;
      line-height: 24px;
      color: #fff; }
  .header .color {
    position: absolute;
    top: 50%;
    left: 10%;
    transform: translateY(-50%);
    width: 90px;
    height: 40px;
    display: flex;
    justify-content: space-between; }
    .header .color .color1 {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background: linear-gradient(180deg, #f287c2 0%, #f1c276 100%);
      cursor: pointer;
      border: none; }
    .header .color .color2 {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background: linear-gradient(0deg, #fca5f1 0%, #b5ffff 100%);
      cursor: pointer;
      border: none; }
  .header .time {
    position: absolute;
    top: 50%;
    right: 10%;
    transform: translateY(-50%);
    font: 14px/1 'Open Sans';
    display: flex;
    flex-direction: column; }
    .header .time .date {
      border: none;
      cursor: pointer; }

@media screen and (max-width: 610px) {
  .header .color {
    left: 2%;
    width: 80px; }
  .header .time {
    display: none; } }
.quote {
  width: 100%;
  height: 4vh; }
  .quote marquee {
